.card {
    margin: 10px;
    width: 200px;
    border: 1px solid rgba(72, 59, 121, 0.664);
    border-radius: 12px;
    background-color: rgba(175, 221, 223, 0.493);
    color: rgba(11, 8, 189, 0.719);
    
  }
  
  .card-title {
    margin-bottom: 15px;
    margin-top: auto;
    font-size: 150%;
  }
  
  #closeIcon {
    display: flex;
    justify-content: flex-end;
    margin-right: 8px;
    margin-top: 8px;
  }
  
  .card-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 0px !important;
    padding-top: 0px !important;
  }

  